<template>
  <div>
    <h3 class="el-icon-arrow-left">编辑文章</h3>
    <el-form label-width="80px" :model="articleInfo">
      <el-form-item label="序号">
        <el-input v-model="articleInfo.id" disabled></el-input>
      </el-form-item>
      <el-form-item label="文章标题">
        <el-input v-model="articleInfo.title"></el-input>
      </el-form-item>
      <el-form-item label="类型">
        <el-input v-model="articleInfo.type"></el-input>
      </el-form-item>
      <el-form-item label="日期">
        <el-input v-model="articleInfo.date" placeholder="/例如2022-3-20"></el-input>
      </el-form-item>
      <el-form-item label="图片地址">
        <el-input v-model="articleInfo.imgUrl"></el-input>
      </el-form-item>
      <el-form-item label="标签">
        <el-input v-model="articleInfo.label"></el-input>
      </el-form-item>
      <el-form-item label="文章内容">
        <quill-editor v-model="articleInfo.content"></quill-editor>
      </el-form-item>
      <el-button v-if=flag type="primary" class="btnAdd" @click="addArticle()">添加文章</el-button>
       <el-button v-else type="primary" class="btnAdd" @click="addArticle()">修改文章</el-button>
    </el-form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //判断是添加文章还是编辑文章
      flag: sessionStorage.isAddArticle,
      //设置文章信息表单
      articleInfo: {
        id: 0,
        title: '',
        type: '',
        date: '',
        imgUrl: '',
        label: [],
        //富文本编辑器的内容
        content: '',
      },
    }
  },
  created() {
    this.articleInfo.id = window.sessionStorage.articleListLength;
   
    //向服务器请求需要编辑的文章
    if(!JSON.parse(this.flag)) {
      this.getEditArticle();
      console.log('请求数据成功');
    }
  },
  mounted() {},
  methods: {
    //提交文章信息表单（添加和修改）
   async  addArticle() {
     const res = await this.$http.post('admin/addArticle',this.articleInfo);
     if(res.status !=200) return this.$message.error(res.msg);
     this.$message.success(res.msg)
    },
    //获取需要编辑的文章
    async getEditArticle() {
      let id = window.sessionStorage.editId;
      const res = await this.$http.get('admin/editArticle/'+id);
      console.log(res);
      this.articleInfo = res;
    }
  },
  watch: {
    $route: function() {

    }
  }
}
</script>

<style scoped>
.btnAdd {
  margin-top: 20px;
}
</style>
